@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #0e042e;
    font-family: 'Poppins', sans-serif;
}

.App {
    width: 100vw;
    overflow-x: hidden;
}
.nav-container {
    background: #090341;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    max-height: 70px;
    align-items: center;
    margin-bottom: 20px;
    width: 100vw;
}

.logo {
    width: 60px;
    height: 60px;
}

.navLinks {
    display: flex;
    list-style: none;
    justify-content: space-between;
    align-items: center;
    font-size: 17px;
    margin-right: 10px;
}

.navLinks .links {
    color: white;
    text-decoration: none;
    font-size: 18px;
    margin-right: 30px;
}

.navLinks .links:hover {
    border-bottom: 2px solid #6846da;
}

.formContent {
    display: flex;
    justify-content: center;
    align-items: center;
}

.search {
    font-size: 17px;
    border: none;
    border-radius: 20px;
    outline: none;
    padding: 10px;
    width: 300px;
    transition: all 0.3s ease;
    background: rgb(170, 169, 169);
}
.search:focus {
    background: white;
}

.icono {
    color: white;
    font-size: 34px;
    padding-left: 9px;
}

.container {
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    list-style: none;
}

.page {
    padding: 7px;
    background: #301588;
    border: 1px solid #dcdcdc;
    border-radius: 6px;
    min-width: 30px;
    min-width: 31px;
    min-height: 10px;
    min-height: 11px;
    text-align: center;
    margin-right: 10px;
    cursor: pointer;
    color: white;

}

.disabled {
    cursor: not-allowed;
}

.active {
    border: 2px solid #fff;
    font-weight: bold;
}

.previous {
    padding: 7px;
    border-radius: 6px;
    margin-right: 10px;
    cursor: pointer;
}

.break {
    padding: 7px;
}

.next {
    padding: 7px;
    border-radius: 6px;
    margin-right: 10px;
    cursor: pointer;
}

.card-img {
    max-width: 100%;
    min-width: 100%;
    min-height: 300px;
    max-height: 300px;
    border-radius: 15px;
}

.card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 30px;
}

.card-info {
    display: flex;
    justify-content: space-between;
    background: #301588;
    /* align-items: center; */
    border-radius: 5px;
    overflow: hidden;
    min-height: 70px;
    max-height: 71px;
}

.card-info .card-name {
    color: white;
    font-size: 15px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
}

.card-subDiv {
    width: 200px;
    margin: 0.8rem;
    overflow: hidden;
}

